'use strict';

var axios = require('axios');

var querystring = require('querystring');

var crypto = require('hash.js');

var parser = require('fast-xml-parser');

function getChecksum(callName, queryParams, sharedSecret) {
  return crypto.sha1().update("".concat(callName).concat(querystring.encode(queryParams)).concat(sharedSecret)).digest('hex');
}

function constructUrl(host, salt, action, params) {
  params.checksum = getChecksum(action, params, salt);
  return "".concat(host, "/api/").concat(action, "?").concat(querystring.encode(params));
}

function httpClient(url) {
  return axios(url, {
    headers: {
      Accept: 'text/xml, application/json, text/plain, */*'
    }
  }).then(function (response) {
    return response.data;
  }).then(function (xml) {
    return parser.parse(xml).response;
  });
}

function normalizeUrl(url) {
  return /\/$/.test(url) ? url.slice(0, -1) : url;
}

function getPathname(url, host) {
  return url.replace(host, '');
}

module.exports = {
  httpClient: httpClient,
  constructUrl: constructUrl,
  normalizeUrl: normalizeUrl,
  getPathname: getPathname
};